<template>
  <div class="calculator-container">
    <div class="page-header">
      <h1>üßÆ ÁßëÂ≠¶ËÆ°ÁÆóÂô®</h1>
      <p class="page-description">ÂäüËÉΩÂÆåÊï¥ÁöÑÁßëÂ≠¶ËÆ°ÁÆóÂô®</p>
    </div>

    <div class="calculator">
      <div class="display">
        <div class="expression">{{ expression }}</div>
        <div class="result">{{ display }}</div>
      </div>

      <div class="buttons">
        <div class="button-row">
          <button class="btn function" @click="clear">C</button>
          <button class="btn function" @click="clearEntry">CE</button>
          <button class="btn function" @click="backspace">‚å´</button>
          <button class="btn operator" @click="appendOperator('/')">√∑</button>
        </div>
        
        <div class="button-row">
          <button class="btn number" @click="appendNumber('7')">7</button>
          <button class="btn number" @click="appendNumber('8')">8</button>
          <button class="btn number" @click="appendNumber('9')">9</button>
          <button class="btn operator" @click="appendOperator('*')">√ó</button>
        </div>
        
        <div class="button-row">
          <button class="btn number" @click="appendNumber('4')">4</button>
          <button class="btn number" @click="appendNumber('5')">5</button>
          <button class="btn number" @click="appendNumber('6')">6</button>
          <button class="btn operator" @click="appendOperator('-')">-</button>
        </div>
        
        <div class="button-row">
          <button class="btn number" @click="appendNumber('1')">1</button>
          <button class="btn number" @click="appendNumber('2')">2</button>
          <button class="btn number" @click="appendNumber('3')">3</button>
          <button class="btn operator" @click="appendOperator('+')">+</button>
        </div>
        
        <div class="button-row">
          <button class="btn number" @click="appendNumber('0')">0</button>
          <button class="btn number" @click="appendNumber('.')">.</button>
          <button class="btn function" @click="negate">¬±</button>
          <button class="btn equals" @click="calculate">=</button>
        </div>

        <div class="scientific-functions">
          <button class="btn function" @click="appendFunction('sin(')">sin</button>
          <button class="btn function" @click="appendFunction('cos(')">cos</button>
          <button class="btn function" @click="appendFunction('tan(')">tan</button>
          <button class="btn function" @click="appendFunction('sqrt(')">‚àö</button>
          <button class="btn function" @click="appendOperator('^')">x¬≤</button>
          <button class="btn function" @click="appendFunction('log(')">log</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CalculatorView',
  metaInfo: {
    // Ê†áÈ¢òÔºöÁ™ÅÂá∫Ê†∏ÂøÉÂäüËÉΩÔºàÁßëÂ≠¶ËÆ°ÁÆóÔºâ„ÄÅÂ∑•ÂÖ∑Á±ªÂûãÔºàÂú®Á∫øÔºâÂíåÊ∂µÁõñÁöÑÊï∞Â≠¶ËåÉÂõ¥
    title: 'ÁßëÂ≠¶ËÆ°ÁÆóÂô® - ÂÖçË¥πÂú®Á∫øÊï∞Â≠¶„ÄÅ‰∏âËßíÂáΩÊï∞„ÄÅÂØπÊï∞„ÄÅÊåáÊï∞Á≠âÂÆåÊï¥ÂäüËÉΩËÆ°ÁÆóÂô®',

    meta: [
      // ÊèèËø∞ÔºöËØ¶ÁªÜËØ¥ÊòéËÆ°ÁÆóÂô®ÁöÑ‰∏ì‰∏öÊÄßÂíåÂäüËÉΩË¶ÜÁõñËåÉÂõ¥ÔºåÂº∫Ë∞ÉÂÖ∂Êñπ‰æøÊÄß
      {
        name: 'description',
        content: 'ÂÖçË¥πÂú®Á∫øÁßëÂ≠¶ËÆ°ÁÆóÂô®ÔºåÂäüËÉΩÂÆåÊï¥„ÄÇÊîØÊåÅ‰∏âËßíÂáΩÊï∞Ôºàsin, cos, tanÔºâ„ÄÅÂØπÊï∞„ÄÅÊåáÊï∞„ÄÅÂπ≥ÊñπÊ†π„ÄÅÈò∂‰πò„ÄÅÊã¨Âè∑ËøêÁÆóÁ≠âÂ§çÊùÇÊï∞Â≠¶ËøêÁÆó„ÄÇÊó†ÈúÄ‰∏ãËΩΩÔºåÊâìÂºÄÊµèËßàÂô®Âç≥ÂèØÈöèÊó∂ËÆ°ÁÆó„ÄÇ'
      },

      // ÂÖ≥ÈîÆËØçÔºöË¶ÜÁõñÊâÄÊúâÊ†∏ÂøÉÁöÑÊï∞Â≠¶ÂäüËÉΩÊúØËØ≠ÂíåÈÄöÁî®ÊêúÁ¥¢ËØç
      {
        name: 'keywords',
        content: 'ÁßëÂ≠¶ËÆ°ÁÆóÂô®, Âú®Á∫øËÆ°ÁÆóÂô®, Êï∞Â≠¶ËÆ°ÁÆóÂô®, ‰∏âËßíÂáΩÊï∞ËÆ°ÁÆó, ÂØπÊï∞ËÆ°ÁÆó, ÂÖçË¥πËÆ°ÁÆóÂô®, Â§çÊùÇËøêÁÆó, sin cos tan'
      }
    ]
  },
  data() {
    return {
      display: '0',
      expression: '',
      lastResult: null,
      shouldResetDisplay: false
    }
  },
  methods: {
    appendNumber(num) {
      if (this.shouldResetDisplay) {
        this.display = num
        this.shouldResetDisplay = false
      } else {
        this.display = this.display === '0' ? num : this.display + num
      }
    },

    appendOperator(op) {
      if (this.expression && !this.shouldResetDisplay) {
        this.calculate()
      }
      this.expression = this.display + ' ' + op + ' '
      this.shouldResetDisplay = true
    },

    appendFunction(func) {
      this.expression = func
      this.shouldResetDisplay = true
    },

    calculate() {
      try {
        let expr = this.expression + this.display
        
        // ÊõøÊç¢ÊòæÁ§∫Á¨¶Âè∑‰∏∫ËÆ°ÁÆóÁ¨¶Âè∑
        expr = expr.replace(/√ó/g, '*').replace(/√∑/g, '/')
        
        // Â§ÑÁêÜÁßëÂ≠¶ÂáΩÊï∞
        expr = expr.replace(/sin\(/g, 'Math.sin(')
        expr = expr.replace(/cos\(/g, 'Math.cos(')
        expr = expr.replace(/tan\(/g, 'Math.tan(')
        expr = expr.replace(/sqrt\(/g, 'Math.sqrt(')
        expr = expr.replace(/log\(/g, 'Math.log10(')
        expr = expr.replace(/\^/g, '**')
        
        const result = eval(expr)
        this.display = result.toString()
        this.expression = ''
        this.shouldResetDisplay = true
        this.lastResult = result
      } catch (error) {
        this.display = 'Error'
        this.expression = ''
        this.shouldResetDisplay = true
      }
    },

    clear() {
      this.display = '0'
      this.expression = ''
      this.shouldResetDisplay = false
    },

    clearEntry() {
      this.display = '0'
    },

    backspace() {
      if (this.display.length > 1) {
        this.display = this.display.slice(0, -1)
      } else {
        this.display = '0'
      }
    },

    negate() {
      if (this.display !== '0') {
        this.display = this.display.startsWith('-') 
          ? this.display.slice(1) 
          : '-' + this.display
      }
    }
  }
}
</script>

